<template>
  <div class="record-type-list">
    <el-card>
      <template #header>
        <div class="card-header">
          <span>记录类型管理</span>
          <el-button type="primary" @click="handleCreate">
            <el-icon><Plus /></el-icon>
            新增类型
          </el-button>
        </div>
      </template>

      <!-- 搜索栏 -->
      <div class="search-bar">
        <el-form :model="searchForm" inline>
          <el-form-item label="类型名称">
            <el-input v-model="searchForm.name" placeholder="请输入类型名称" clearable />
          </el-form-item>
          <el-form-item label="状态">
            <el-select v-model="searchForm.status" placeholder="请选择状态" clearable>
              <el-option label="启用" value="active" />
              <el-option label="禁用" value="inactive" />
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearch">搜索</el-button>
            <el-button @click="handleReset">重置</el-button>
          </el-form-item>
        </el-form>
      </div>

      <!-- 记录类型表格 -->
      <el-table :data="recordTypes" v-loading="loading" stripe>
        <el-table-column prop="id" label="ID" width="80" />
        <el-table-column prop="name" label="类型名称" />
        <el-table-column prop="description" label="描述" />
        <el-table-column prop="fields" label="字段数量">
          <template #default="{ row }">
            <el-tag>{{ row.fields?.length || 0 }} 个字段</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态">
          <template #default="{ row }">
            <el-tag :type="row.status === 'active' ? 'success' : 'danger'">
              {{ row.status === 'active' ? '启用' : '禁用' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="created_at" label="创建时间" />
        <el-table-column label="操作" width="200">
          <template #default="{ row }">
            <el-button size="small" @click="handleEdit(row)">编辑</el-button>
            <el-button size="small" type="info" @click="handleFields(row)">字段</el-button>
            <el-button size="small" type="danger" @click="handleDelete(row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          v-model:current-page="pagination.page"
          v-model:page-size="pagination.size"
          :total="pagination.total"
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus } from '@element-plus/icons-vue'

// 响应式数据
const loading = ref(false)
const recordTypes = ref([])

const searchForm = reactive({
  name: '',
  status: ''
})

const pagination = reactive({
  page: 1,
  size: 10,
  total: 0
})

// 方法
const fetchRecordTypes = async () => {
  loading.value = true
  try {
    // TODO: 调用API获取记录类型列表
    // const response = await recordTypeApi.getRecordTypes({
    //   ...searchForm,
    //   page: pagination.page,
    //   size: pagination.size
    // })
    // recordTypes.value = response.data.items
    // pagination.total = response.data.total
    
    // 模拟数据
    recordTypes.value = []
    pagination.total = 0
  } catch (error) {
    ElMessage.error('获取记录类型列表失败')
  } finally {
    loading.value = false
  }
}

const handleSearch = () => {
  pagination.page = 1
  fetchRecordTypes()
}

const handleReset = () => {
  Object.assign(searchForm, {
    name: '',
    status: ''
  })
  handleSearch()
}

const handleCreate = () => {
  // TODO: 打开创建记录类型对话框
  ElMessage.info('创建记录类型功能开发中')
}

const handleEdit = (row: any) => {
  // TODO: 打开编辑记录类型对话框
  ElMessage.info('编辑记录类型功能开发中')
}

const handleFields = (row: any) => {
  // TODO: 打开字段配置对话框
  ElMessage.info('字段配置功能开发中')
}

const handleDelete = async (row: any) => {
  try {
    await ElMessageBox.confirm('确定要删除该记录类型吗？', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
    
    // TODO: 调用删除API
    ElMessage.success('删除成功')
    fetchRecordTypes()
  } catch (error) {
    // 用户取消删除
  }
}

const handleSizeChange = (size: number) => {
  pagination.size = size
  fetchRecordTypes()
}

const handleCurrentChange = (page: number) => {
  pagination.page = page
  fetchRecordTypes()
}

// 生命周期
onMounted(() => {
  fetchRecordTypes()
})
</script>

<style scoped>
.record-type-list {
  padding: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-bar {
  margin-bottom: 20px;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}
</style>